import { Link, useLocation } from 'react-router-dom';
import logoImage from './img/logo-purple3.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook } from '@fortawesome/free-brands-svg-icons';
import { faYoutube } from '@fortawesome/free-brands-svg-icons';
import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import { faXmark } from '@fortawesome/free-solid-svg-icons';

import { faUser } from '@fortawesome/free-solid-svg-icons';
import { faGraduationCap } from '@fortawesome/free-solid-svg-icons';
import { faCode } from '@fortawesome/free-solid-svg-icons';
import { faDiagramProject } from '@fortawesome/free-solid-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { useState , useEffect} from 'react';
import './nav/Navbar.css';
import Navigationcode from './navigationcode';


function Navbar() {
  // navigations-----------------------------------------------------------------
  const {goHome, goAbout, goEducation, goSkills, goProjects, goContact} = Navigationcode();
  // nav active------------------------------------------------------------------
  const location = useLocation();
  const [activeNav, setActiveNav] = useState(location.pathname);

  const handleNavClick = (path) => {
    setActiveNav(path);
  };

  
  return (
    <>
      {/*side nav------------------------------------------------------------------*/}
        <div className="side-nav-box">
        <button onClick={()=>handleNavClick(goAbout)} className={activeNav === "/about" ? "side-nav-item-common side-nav-item-about side-nav-active" : "side-nav-item-common side-nav-item-about"} data-before="AboutME"><FontAwesomeIcon icon={faUser} /></button>
        <button onClick={()=>handleNavClick(goEducation)} className={activeNav === "/education" ? "side-nav-item-common side-nav-item-education side-nav-active" : "side-nav-item-common side-nav-item-about"} data-before="MyEDUCATION"><FontAwesomeIcon icon={faGraduationCap} /></button>
        <button onClick={()=>handleNavClick(goSkills)} className={activeNav === "/skills" ? "side-nav-item-common side-nav-item-skills side-nav-active" : "side-nav-item-common side-nav-item-about"} data-before="MySKILLS"><FontAwesomeIcon icon={faCode} /></button>
        <button onClick={()=>handleNavClick(goProjects)} className={activeNav === "/projects" ? "side-nav-item-common side-nav-item-projects side-nav-active" : "side-nav-item-common side-nav-item-about"} data-before="MyPROJECTS"><FontAwesomeIcon icon={faDiagramProject} /></button>
        <button onClick={()=>handleNavClick(goContact)} className={activeNav === "/contact" ? "side-nav-item-common side-nav-item-contact side-nav-active" : "side-nav-item-common side-nav-item-about"} data-before="MyCONTACT"><FontAwesomeIcon icon={faEnvelope} /></button>
        </div>

      <div className="container-fluid container-fluid-nooverflow  nav-container">
        

        {/*top nav------------------------------------------------------------------*/}

        <div className="row justify-content-around align-items-center">
{/*this is logo here*/}
          <div className="col-4 col-sm-4 col-md-2 col-lg-2 col-xl-2 col-xxl-2 nav-logo-box">
            <img onClick={goHome} src={logoImage} alt="logo Image" loading="lazy"/> 
          </div>

          <div className="col-6 col-sm-6 col-md-8 col-lg-8 col-xl-8 col-xxl-8 nav-link-box">
            <a href="https://www.facebook.com/profile.php?id=100009095791567" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="Connect! Mukesh Kumar
                 ">
              <FontAwesomeIcon icon={faFacebook} />
            </a>
            <a href="https://www.instagram.com/im_mukesh_9/" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="Follow! @im_mukesh_9">
              <FontAwesomeIcon icon={faInstagram} />
            </a>
            <a href="https://github.com/MukeshGit9" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="MukeshGit9">
              <FontAwesomeIcon icon={faGithub} />
            </a>
            <a href="https://www.youtube.com/@TechnoXpress" target="_blank" rel="noopener noreferrer" className="nav-social-icon" title="Subscribe! @TechnoXpress">
              <FontAwesomeIcon icon={faYoutube} />
            </a>
          </div>

        </div>
      </div>

    </>
  );
}

export default Navbar;
